import React, { useState } from "react";
import { developerDelete } from "../server/server";

export function DeveloperList(props) {
  const searchOptions = [
    "특급",
    "고급",
    "중급",
    "초급",
    "react",
    "vue",
    "은행",
    "카드",
    "쇼핑몰",
  ];

  const { developers, openModal, refreshDeveloperList } = props;
  const [currentDeveloper, setCurrentDeveloper] = useState(null);

  const [searchTerm, setSearchTerm] = useState(""); // 검색어 상태
  const [selectedOptions, setSelectedOptions] = useState([]); // 체크박스 선택된 옵션 상태
  const [includeAll, setIncludeAll] = useState(false); // 모두 포함 체크박스 상태
  const [isSearchActive, setIsSearchActive] = useState(false); // 검색이 활성화되었는지 여부
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;

  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;

  const handleSearch = () => {
    setIsSearchActive(true); // 검색 활성화
    setCurrentPage(1); // 페이지를 첫 페이지로 초기화
  };

  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      handleSearch();
    }
  };

  const handleCheckboxChange = (option) => {
    setSelectedOptions((prev) =>
      prev.includes(option)
        ? prev.filter((item) => item !== option)
        : [...prev, option]
    );
    updateSearchTerm(option);
  };

  const handleIncludeAllChange = () => {
    setIncludeAll((prev) => !prev);
  };

  const updateSearchTerm = (option) => {
    const terms = searchTerm.split(" ").filter(Boolean);
    if (terms.includes(option)) {
      setSearchTerm(terms.filter((term) => term !== option).join(" "));
    } else {
      setSearchTerm([...terms, option].join(" "));
    }
  };

  const filteredDevelopers = isSearchActive
    ? developers.filter((dev) => {
        const terms = searchTerm.toLowerCase().split(" ").filter(Boolean);

        if (includeAll) {
          return terms.every(
            (term) =>
              (dev.devRank && dev.devRank.toLowerCase().includes(term)) ||
              (dev.devSkillsOne &&
                dev.devSkillsOne.toLowerCase().includes(term)) ||
              (dev.devSkillsTwo &&
                dev.devSkillsTwo.toLowerCase().includes(term))
          );
        } else {
          return terms.some(
            (term) =>
              (dev.devRank && dev.devRank.toLowerCase().includes(term)) ||
              (dev.devSkillsOne &&
                dev.devSkillsOne.toLowerCase().includes(term)) ||
              (dev.devSkillsTwo &&
                dev.devSkillsTwo.toLowerCase().includes(term))
          );
        }
      })
    : developers;

  const currentDevelopers = filteredDevelopers.slice(
    indexOfFirstItem,
    indexOfLastItem
  );

  const totalPages = Math.ceil(filteredDevelopers.length / itemsPerPage);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const renderPageNumbers = () => {
    const pageNumbers = [];
    for (let i = 1; i <= totalPages; i++) {
      pageNumbers.push(
        <button
          key={i}
          onClick={() => handlePageChange(i)}
          className={i === currentPage ? "active" : ""}
        >
          {i}
        </button>
      );
    }
    return pageNumbers;
  };

  const handleReset = () => {
    setSearchTerm("");
    setSelectedOptions([]);
    setIncludeAll(false);
    setIsSearchActive(false); // 검색 비활성화
    setCurrentPage(1);
  };

  const handleDetail = (dev) => {
    setCurrentDeveloper(dev);
    openModal(dev);
  };

  const handelInsert = () => {
    setCurrentDeveloper(null);
    openModal(null);
  };

  const handleDelete = async (dev) => {
    if (window.confirm("삭제하시겠습니까?")) {
      try {
        await developerDelete(dev); // 삭제 API 호출
        alert("삭제되었습니다.");
        refreshDeveloperList(); // 목록 갱신 함수 호출
      } catch (error) {
        alert("삭제 실패: " + error.message);
      }
    }
  };

  return (
    <div>
      <div>
        <h1>개발자 서칭</h1>
      </div>
      <div className="devSearch">
        <div>
          <label>
            <input
              type="checkbox"
              checked={includeAll}
              onChange={handleIncludeAllChange}
            />
            모두포함
          </label>
          <input
            type="text"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder="검색어 입력(등급 및 기술) 단어 한칸 띄어쓰기"
          />
          <div>
            {searchOptions.map((option) => (
              <label key={option}>
                <input
                  type="checkbox"
                  checked={selectedOptions.includes(option)}
                  onChange={() => handleCheckboxChange(option)}
                />
                {option}
              </label>
            ))}
          </div>

          <button onClick={handleSearch}>검색</button>
          <button onClick={handleReset}>초기화</button>
          <button onClick={handelInsert}>개발자 등록</button>
        </div>
        <div>
          <table>
            <thead>
              <tr>
                <td
                  style={{
                    color: "white",
                    backgroundColor: "#5b6671",
                    fontSize: "18px",
                  }}
                >
                  검색 : {filteredDevelopers.length}명
                </td>
              </tr>
              <tr>
                <th>등급</th>
                <th>연차</th>
                <th>단가</th>
                <th>이름</th>
                <th>연락처</th>
                <th>거주지</th>
                <th>프로젝트 가능거리</th>
                <th>투입가능시기</th>
                <th>인터뷰가능시기</th>
                <th>개발/운영</th>
                <th>수정</th>
                <th>삭제</th>
              </tr>
            </thead>
            <tbody>
              {currentDevelopers.length > 0 ? (
                currentDevelopers.map((dev) => (
                  <tr key={dev.id}>
                    <td>{dev.devRank}</td>
                    <td>{dev.devCarrer}</td>
                    <td>{dev.devPrice}</td>
                    <td>{dev.devName}</td>
                    <td>{dev.devPhone}</td>
                    <td>{dev.devLoc}</td>
                    <td>{dev.devProjLoc}</td>
                    <td>{dev.devStartTime}</td>
                    <td>{dev.devInterviewTime}</td>
                    <td>{dev.devSiSm}</td>
                    <td>
                      <input
                        type="button"
                        value="수정"
                        onClick={() => handleDetail(dev)}
                      />
                    </td>
                    <td>
                      <input
                        type="button"
                        value="삭제"
                        onClick={() => handleDelete(dev)}
                      />
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="11">검색된 데이터가 없습니다</td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
        <div className="pagination">{renderPageNumbers()}</div>
      </div>
    </div>
  );
}
