import { useEffect, useState } from "react";

export function ProjectList() {
  const [projectData, setProjectData] = useState([]);

  useEffect(() => {
    const dbData = async () => {
      const res = await fetch("http://192.168.0.19:8080/giant_project_list");
      const rs = await res.json();
      setProjectData(rs);
    };
    dbData();
  }, []);

  const toggleSub = (id) => {
    setProjectData((prevData) =>
      prevData.map((project) =>
        project.id === id
          ? { ...project, isDetailVisible: !project.isDetailVisible }
          : project
      )
    );
  };
  return (
    <div className="projectList">
      <div>
        <h1>프로젝트 정보 게시판</h1>
      </div>
      <div>
        <table className="tableList">
          <thead>
            <tr>
              <th>등급</th>
              <th>프로젝트명(상세내용 : 제목클릭)</th>
              <th>시작일</th>
              <th>기간</th>
              <th>위치</th>
              <th>필요기술</th>
              <th>단가(만)</th>
              <th>진행여부</th>
              <th>지원체크</th>
            </tr>
          </thead>
          <tbody>
            {projectData.map((data, index) => (
              <>
                <tr
                  key={`key-${index}`}
                  className="listTitle"
                  onClick={() => toggleSub(data.id)}
                >
                  <td>{data.rank}</td>
                  <td>{data.project_name}</td>
                  <td>{data.startDate}</td>
                  <td>{data.projectTerm}</td>
                  <td>{data.loc}</td>
                  <td>{data.skill}</td>
                  <td>{data.price}</td>
                  <td>{data.use}</td>
                  <td>
                    <button>지원</button>
                  </td>
                </tr>
                {data.isDetailVisible && (
                  <tr key={`detail-${data.id}`} className="listSub">
                    <td colSpan="9" style={{ textAlign: "left" }}>
                      {data.detailData}
                    </td>
                  </tr>
                )}
              </>
            ))}
          </tbody>
          <tfoot></tfoot>
        </table>
      </div>
    </div>
  );
}
