import { Link } from "react-router-dom";

export function Header() {
  return (
    <div>
      <div className="headerDv">
        <Link to="/login">
          <span className="topMenu" style={{ textDecoration: "none" }}>
            로그인
          </span>
        </Link>
        <Link to="/">
          <span className="topMenu">프로젝트 정보</span>
        </Link>
        <Link to="/login">
          <span className="topMenu">내 지원정보</span>
        </Link>
        <Link to="/regist">
          <span className="topMenu">프로젝트 등록</span>
        </Link>
        <Link to="/devSearch">
          <span className="topMenu">개발자 서칭</span>
        </Link>
      </div>
    </div>
  );
}
