import React, { useEffect, useState } from "react";
import DeveloperRegi from "./DeveloperRegi";
import { DeveloperList } from "./DeveloperList";
import { developerDetail, developerSearch } from "../server/server";

export function DeveloperSearch() {
  const [developers, setDevelopers] = useState([]);
  const [error, setError] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [currentDeveloper, setCurrentDeveloper] = useState(null);
  const [isEditMode, setIsEditMode] = useState(false);

  const openModal = async (dev) => {
    console.log("test::" + dev);
    if (dev) {
      const detailData = await developerDetail(dev);
      setCurrentDeveloper(detailData);
      setIsEditMode(true);
    } else {
      setCurrentDeveloper(null);
      setIsEditMode(false);
    }
    setIsModalOpen(true);
  };
  const closeModal = () => {
    setIsModalOpen(false);
    setCurrentDeveloper(null);
    setIsEditMode(false);
  };

  const loadDevelopers = async () => {
    try {
      const data = await developerSearch();
      setDevelopers(data);
    } catch (error) {
      setError(error.message);
    }
  };
  useEffect(() => {
    loadDevelopers();
  }, []);

  return (
    <div>
      <DeveloperList
        developers={developers}
        openModal={openModal}
        refreshDeveloperList={loadDevelopers}
      />
      {isModalOpen && (
        <DeveloperRegi
          closeModal={closeModal}
          error={error}
          refreshDeveloperList={loadDevelopers}
          developer={currentDeveloper}
          isEditMode={isEditMode}
        />
      )}
    </div>
  );
}
