export const developerSearch = async () => {
  try {
    const response = await fetch(
      "http://www.giantsolution.co.kr/server/devSearch"
    );
    if (!response.ok) {
      throw new Error("데이터를 가져오는 데 실패했습니다.");
    }
    const data = await response.json();
    return data;
  } catch (error) {
    alert("검색에러", error);
    throw error;
  }
};

export const developerInsert = async (developer) => {
  try {
    const response = await fetch(
      "http://125.133.48.26:8030/projectDataInsert",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(developer),
      }
    );

    if (!response.ok) {
      throw new Error("등록에 실패했습니다.");
    }

    const data = await response.json();
    return data;
  } catch (error) {
    alert("등록에러: " + error.message);
    throw error;
  }
};

