import React, { useRef, useState, useEffect } from "react";
import { developerInsert, developerUpdate } from "../server/server";

const DeveloperRegi = (props) => {
  const { closeModal, refreshDeveloperList, developer, isEditMode } = props; // 등록 후 목록 갱신을 위해 refreshDeveloperList 추가

  const devRankRef = useRef();
  const devCarrerRef = useRef();
  const devPriceRef = useRef();
  const devNameRef = useRef();
  const devPhoneRef = useRef();
  const devLocRef = useRef();
  const devStartTimeRef = useRef();
  const devInterviewTimeRef = useRef();
  const devSiSmRef = useRef();
  const devProjUseRef = useRef();
  const devProjLocRef = useRef();
  const devProjEndRef = useRef();
  const devBirthRef = useRef();
  const devSkillsOneRef = useRef();
  const devSkillsTwoRef = useRef();
  const devOtherRef = useRef();

  const [skillsOneLength, setSkillsOneLength] = useState(0);
  const [skillsTwoLength, setSkillsTwoLength] = useState(0);

  const [isSkillsOneOverLimit, setIsSkillsOneOverLimit] = useState(false);
  const [isSkillsTwoOverLimit, setIsSkillsTwoOverLimit] = useState(false);

  useEffect(() => {
    if (developer && isEditMode) {
      devRankRef.current.value = developer.devRank || "--";
      devCarrerRef.current.value = developer.devCarrer || "--";
      devPriceRef.current.value = developer.devPrice || "--";
      devNameRef.current.value = developer.devName || "--";
      devPhoneRef.current.value = developer.devPhone || "--";
      devLocRef.current.value = developer.devLoc || "--";
      devStartTimeRef.current.value = developer.devStartTime || "--";
      devInterviewTimeRef.current.value = developer.devInterviewTime || "--";
      devSiSmRef.current.value = developer.devSiSm || "--";
      devProjUseRef.current.value = developer.devProjUse || "--";
      devProjLocRef.current.value = developer.devProjLoc || "--";
      devProjEndRef.current.value = developer.devProjEnd || "--";
      devBirthRef.current.value = developer.devBirth || "--";
      devSkillsOneRef.current.value = developer.devSkillsOne || "--";
      devSkillsTwoRef.current.value = developer.devSkillsTwo || "--";
      devOtherRef.current.value = developer.devOther || "--";

      setSkillsOneLength(developer.devSkillsOne?.length || 0);
      setSkillsTwoLength(developer.devSkillsTwo?.length || 0);
    }
  }, [developer, isEditMode]);

  const handleInsert = async () => {
    const developerInput = {
      devRank: devRankRef.current.value || "--",
      devCarrer: devCarrerRef.current.value || 0,
      devPrice: devPriceRef.current.value || "--",
      devName: devNameRef.current.value || "--",
      devPhone: devPhoneRef.current.value || "--",
      devLoc: devLocRef.current.value || "--",
      devStartTime: devStartTimeRef.current.value || "--",
      devInterviewTime: devInterviewTimeRef.current.value || "--",
      devSiSm: devSiSmRef.current.value || "--",
      devProjUse: devProjUseRef.current.value || "--",
      devProjLoc: devProjLocRef.current.value || "--",
      devProjEnd: devProjEndRef.current.value || "--",
      devBirth: devBirthRef.current.value || "--",
      devSkillsOne: devSkillsOneRef.current.value || "--",
      devSkillsTwo: devSkillsTwoRef.current.value || "--",
      devOther: devOtherRef.current.value || "--",
      ...(isEditMode && { id: developer.id }),
    };

    const onelength = calculateLength(devSkillsOneRef.current.value);
    const towlength = calculateLength(devSkillsTwoRef.current.value);
    if (onelength > 3000 || towlength > 3000) {
      alert("스킬 입력칸에 3000자가 넘었습니다");
    } else {
      try {
        if (isEditMode) {
          await developerUpdate(developerInput);
          alert("수정완료");
        } else {
          await developerInsert(developerInput);
          alert("등록완료");
        }

        refreshDeveloperList(); // 목록 갱신 함수 호출
        closeModal(); // 모달 닫기
      } catch (error) {
        alert("등록실패: " + error.message);
      }
    }
  };

  const calculateLength = (text) => {
    let length = 0;
    for (let i = 0; i < text.length; i++) {
      const char = text.charCodeAt(i);
      if (char >= 0x4e00 && char <= 0x9fff) {
        // 한자 범위
        length += 2;
      } else if (char >= 0xac00 && char <= 0xd7a3) {
        // 한글 범위
        length += 2;
      } else {
        length += 1;
      }
    }
    return length;
  };

  const handleSkillsOneChange = () => {
    const length = calculateLength(devSkillsOneRef.current.value);
    setSkillsOneLength(calculateLength(devSkillsOneRef.current.value));
    setIsSkillsOneOverLimit(length > 3000);
  };

  const handleSkillsTwoChange = () => {
    const length = calculateLength(devSkillsTwoRef.current.value);
    setSkillsTwoLength(calculateLength(devSkillsTwoRef.current.value));
    setIsSkillsTwoOverLimit(length > 3000);
  };

  const handleNumberInput = (e) => {
    const value = e.target.value;
    if (!/^\d*$/.test(value)) {
      e.target.value = value.replace(/[^\d]/g, "");
    }
  };

  return (
    <div>
      <div className="modal-background">
        <div className="modal-container">
          <div className="devRegi">
            <h2>개발자 등록</h2>
            <div className="input-row">
              <div className="input-group">
                <label>등급 </label>
                <input type="text" ref={devRankRef} />
              </div>
              <div className="input-group">
                <label>연차 </label>
                <input
                  type="text"
                  ref={devCarrerRef}
                  onInput={handleNumberInput}
                />
                <span>년차</span>
              </div>
            </div>
            <div className="input-row">
              <div className="input-group">
                <label>단가 </label>
                <input type="text" ref={devPriceRef} />
              </div>
              <div className="input-group">
                <label>이름 </label>
                <input type="text" ref={devNameRef} />
              </div>
            </div>
            <div className="input-row">
              <div className="input-group">
                <label>연락처 </label>
                <input type="text" ref={devPhoneRef} />
              </div>
              <div className="input-group">
                <label>생년월일 </label>
                <input type="text" ref={devBirthRef} />
              </div>
            </div>
            <div className="input-row">
              <div className="input-group">
                <label>거주지 </label>
                <input type="text" ref={devLocRef} />
              </div>
            </div>
            <div className="input-row">
              <div className="input-group">
                <label>
                  투입 <br></br>가능시기
                </label>
                <input type="text" ref={devStartTimeRef} />
              </div>
              <div className="input-group">
                <label>
                  인터뷰 <br></br>가능시기
                </label>
                <input type="text" ref={devInterviewTimeRef} />
              </div>
            </div>
            <div className="input-row">
              <div className="input-group">
                <label>개발/운영 </label>
                <input type="text" ref={devSiSmRef} />
              </div>
              <div className="input-group">
                <label>프로젝트 진행여부 </label>
                <input type="text" ref={devProjUseRef} />
              </div>
            </div>
            <div className="input-row">
              <div className="input-group">
                <label>프로젝트 가능거리 </label>
                <input type="text" ref={devProjLocRef} />
              </div>
              <div className="input-group">
                <label>프로젝트 종료일 </label>
                <input type="text" ref={devProjEndRef} />
              </div>
            </div>
            <div className="input-row">
              <div className="input-group">
                <label>스킬1 </label>
                <textarea
                  ref={devSkillsOneRef}
                  onChange={handleSkillsOneChange}
                  maxLength="3000"
                  className={isSkillsOneOverLimit ? "over-limit" : ""}
                />
                <span>{skillsOneLength}/3000</span>
              </div>
              <div className="input-group">
                <label>스킬2 </label>
                <textarea
                  ref={devSkillsTwoRef}
                  onChange={handleSkillsTwoChange}
                  maxLength="3000"
                  className={isSkillsTwoOverLimit ? "over-limit" : ""}
                />
                <span>{skillsTwoLength}/3000</span>
              </div>
            </div>
            <div className="input-group">
              <label>기타 </label>
              <textarea ref={devOtherRef} />
            </div>

            <div className="button-row">
              <button onClick={handleInsert}>
                {isEditMode ? "수정" : "등록"}
              </button>
              <button onClick={closeModal}>취소</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DeveloperRegi;
