import React, { useState, useEffect, useRef } from "react";
import flatpickr from "flatpickr";
import "flatpickr/dist/flatpickr.min.css";
import { Korean } from "flatpickr/dist/l10n/ko.js";

export function ProjectRegi() {
  const [rank, setRank] = useState([]);
  const [rankString, setRankString] = useState("");
  const [projectName, setProjectName] = useState("");
  const [startDate, setStartDate] = useState("");
  const [projectTerm, setProjectTerm] = useState("");
  const [loc, setLoc] = useState("");
  const [skill, setSkill] = useState("");
  const [tasks, setTasks] = useState("");
  const [price, setPrice] = useState("");
  const [detailData, setDetailData] = useState("");
  const dateInputRef = useRef(null);

  const months = Array.from({ length: 12 }, (_, index) => `${index + 1}개월`);

  useEffect(() => {
    flatpickr(dateInputRef.current, {
      dateFormat: "Y-m-d",
      locale: Korean,

      onChange: (selectedDates, dateStr, instance) => {
        setStartDate(dateStr);
      },
    });
  }, []);

  useEffect(() => {
    setRankString(rank.join("/"));
  }, [rank]);

  const handleCheckboxChange = (event) => {
    const { value, checked } = event.target;
    setRank((prev) => {
      if (checked) {
        return [...prev, value];
      } else {
        return prev.filter((level) => level !== value);
      }
    });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    const projectData = {
      rank: rankString,
      projectName,
      startDate,
      projectTerm,
      loc,
      skill,
      tasks,
      price,
      detailData,
    };

    try {
      const response = await fetch(
        "http://192.168.0.19:8080/projectDataInsert",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(projectData),
        }
      );

      if (!response.ok) {
        throw new Error("Network response was not ok");
      }

      const responseData = await response.json();
      console.log("Success:", responseData);
    } catch (error) {
      console.error("Error:", error);
    }
  };

  return (
    <form className="projectRegi" onSubmit={handleSubmit}>
      <div>
        등급 :
        <label>
          <input type="checkbox" value="특급" onChange={handleCheckboxChange} />
          특급
        </label>
        <label>
          <input type="checkbox" value="고급" onChange={handleCheckboxChange} />
          고급
        </label>
        <label>
          <input type="checkbox" value="중급" onChange={handleCheckboxChange} />
          중급
        </label>
        <label>
          <input type="checkbox" value="초급" onChange={handleCheckboxChange} />
          초급
        </label>
      </div>
      <div>선택된 등급: {rankString}</div>
      <div>
        프로젝트명 :{" "}
        <input
          type="text"
          value={projectName}
          onChange={(e) => setProjectName(e.target.value)}
        />
      </div>
      <div>
        시작일 :{" "}
        <input
          type="text"
          ref={dateInputRef}
          value={startDate}
          onChange={(e) => setStartDate(e.target.value)}
        />
      </div>
      <div>
        기간 :
        <select
          value={projectTerm}
          onChange={(e) => setProjectTerm(e.target.value)}
        >
          {months.map((month, index) => (
            <option key={index} value={month}>
              {month}
            </option>
          ))}
          <option value="연단위">연단위</option>
        </select>
      </div>
      <div>
        위치 :{" "}
        <input
          type="text"
          value={loc}
          onChange={(e) => setLoc(e.target.value)}
        />
      </div>
      <div>
        스킬 :{" "}
        <input
          type="text"
          value={skill}
          onChange={(e) => setSkill(e.target.value)}
        />
      </div>
      <div>
        업무 :{" "}
        <input
          type="text"
          value={tasks}
          onChange={(e) => setTasks(e.target.value)}
        />
      </div>
      <div>
        단가 :{" "}
        <input
          type="text"
          value={price}
          onChange={(e) => setPrice(e.target.value)}
        />
      </div>
      <div>
        상세정보 :{" "}
        <textarea
          value={detailData}
          onChange={(e) => setDetailData(e.target.value)}
        ></textarea>
      </div>
      <button type="submit" className="btn">
        등록
      </button>
    </form>
  );
}
